@import url(https://rsms.me/inter/inter.css);
*,
*::after,
*::before {
  box-sizing: border-box;
}
html {
  font-family: 'Inter', sans-serif;
}
@supports (font-variation-settings: normal) {
  html {
    font-family: 'Inter var', sans-serif;
  }
}

.app {
  max-width: 1010px;
  margin-left: auto;
  margin-right: auto;
  background-color: #ffffff;
  padding-top: 32px;
  padding-bottom: 78px;
  font-family: 'Inter', serif;
  font-size: 12px;
  position: relative;
}

.pagination {
  text-align: center;
  position: absolute;
  bottom: 18px;
  left: 50%;
  transform: translateX(-50%);
}

.container {
  margin-left: auto;
  margin-right: auto;
  max-width: 938px;
}

.notFoundMessage {
  font-size: 2em;
}

@media (max-width: 420px) {
  .app {
    padding-top: 0px;
    padding-left: 16px;
    padding-right: 16px;
  }

  .ant-rate {
    right: 0;
  }
} ;

*,
*::after,
*::before {
  box-sizing: border-box;
}

.filters {
  width: 146px;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 19.23px;
  padding: 0px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-bottom: solid 1px rgba(0, 0, 0, 0.09);
}

.filters__button {
  width: 64.6px;
  height: 53.77px;
  border: none;
  border-bottom: solid 1px rgba(0, 0, 0, 0.09);
  position: relative;
  top: 1px;
  background-color: #ffffff;
  outline: none;
  transition: 0.5s;
}

.filters__button:hover {
  border-bottom: solid 1px #1890ff;
}

.filters__button:focus {
  border-bottom: solid 1px #1890ff;
}

@media (max-width: 420px) {
  .filters {
    margin-bottom: 17.23px;
  }
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

.search {
  height: 40px;
  width: 100%;
  margin-bottom: 32px;
  border: 1px solid #d9d9d9;
  border-radius: 4px;
}

.search:focus {
  outline: none;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

.movies-list {
  display: grid;
  grid-template-columns: 454px 454px;
  grid-column-gap: 36px;
  -webkit-column-gap: 36px;
          column-gap: 36px;
  grid-row-gap: 35px;
  row-gap: 35px;
  padding: 0;
}

.movie-list__item {
  width: 454px;
  height: 281px;
  box-shadow: 0 0 20px -10px;
}

.spin_scale {
  transform: scale(2);
  display: block;
  margin-top: 100px;
}

@media (max-width: 420px) {
  .movies-list {
    display: flex;
    flex-wrap: wrap;
    grid-template-columns: none;
  }

  .movie-list__item {
    height: 281px;
  }
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

.movie-card {
  height: 100%;
  display: flex;
  flex-direction: row;
  position: relative;
  overflow: hidden;
}

.movie-card__poster {
  width: 183px;
  object-fit: cover;
}

.movie-card__information {
  padding: 12px 20px;
}

.movie-card__title {
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
  margin-top: 0px;
  margin-bottom: 7px;
  max-width: 200px;
  font-size: 20px;
  line-height: 28px;
}

.movie-card__raiting {
  display: flex;
  align-items: center;
  /* justify-content: center; */
  position: absolute;
  width: 30px;
  height: 30px;
  top: 12px;
  right: 9px;
  border: 2px solid #e9d100;
  border-radius: 50%;
}

.movie-card__raiting > p {
  margin: auto;
}

.movie-card__date {
  color: #827e7e;
  margin-top: 0px;
  margin-bottom: 9px;
}

.movie-card__categorys-list {
  display: flex;
  flex-direction: row;
  padding: 0px;
  margin-bottom: 7px;
  flex-wrap: wrap;
}

.movie-card__category {
  color: rgba(0, 0, 0, 0.65);
  background-color: #fafafa;
  margin-right: 8px;
  margin-bottom: 5px;
  line-height: 14.52px;
  height: 20px;
  min-width: 46px;
  padding: 2px 4px 3px 5px;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
}

.movie-card__overview {
  margin: 0px;
  line-height: 22px;
}

.ant-rate {
  position: absolute;
  bottom: 10px;
}

.ant-rate > .ant-rate-star {
  margin-right: 2px;
}

@media (max-width: 420px) {
  .movie-card {
    height: 100%;
    display: grid;
    grid-template-columns: 60px auto;
    grid-template-rows: 1 2fr;
    padding-top: 6px;
    padding-left: 10px;
    padding-bottom: 46px;
    grid-column-gap: 13px;
    -webkit-column-gap: 13px;
            column-gap: 13px;
    grid-row-gap: 10px;
    row-gap: 10px;
  }

  .movie-card__poster {
    width: 60px;
    height: 91px;
  }

  .movie-card__information {
    padding: 0;
    max-width: 290px;
    max-height: 97px;
  }

  .movie-card__raiting {
    top: 5px;
    right: 8px;
  }

  .movie-card__title {
    margin-right: 40px;
  }

  .movie-card__overview {
    grid-row-start: 2;
    grid-column-start: 1;
    grid-column-end: 3;
  }
}

